import '@/app/globals.css';
import { Inter } from 'next/font/google';
import React from "react";
import { LockClosedIcon, MoonIcon, SunIcon } from '@heroicons/react/24/outline';

const inter = Inter({ subsets: ['latin'] });

export default function RootLayout({ children }: { children: React.ReactNode }) {
    // Dynamic title based on the current path (handled in child components)
    const [isDarkMode, setIsDarkMode] = React.useState(true); // Default to dark mode

    const toggleDarkMode = () => {
        setIsDarkMode(!isDarkMode);
        document.documentElement.classList.toggle('dark');
    };

    return (
        <html lang="en">
        <body className={`${inter.className} bg-gray-100 dark:bg-gray-900 min-h-screen`}>
        {/* Top Branding Bar */}
        <header className="bg-gray-800 dark:bg-gray-700 p-4 sticky top-0 z-10">
            <div className="flex items-center justify-between max-w-4xl mx-auto">
                <div className="flex items-center text-gray-100 text-2xl font-bold">
                    <LockClosedIcon className="h-6 w-6 mr-2 text-gray-400" />
                    Secure Note
                </div>
                {/* Dark/Light Mode Toggle */}
                <button
                    onClick={toggleDarkMode}
                    className="p-2 rounded-full bg-gray-700 dark:bg-gray-600 text-gray-100 hover:bg-gray-600 dark:hover:bg-gray-500 transition duration-200"
                >
                    {isDarkMode ? (
                        <SunIcon className="h-6 w-6" />
                    ) : (
                        <MoonIcon className="h-6 w-6" />
                    )}
                </button>
            </div>
        </header>

        {/* Main Content Area */}
        <main className="p-8">
            <div className="max-w-4xl mx-auto bg-gray-50 dark:bg-gray-800 p-8 rounded-lg text-gray-900 dark:text-gray-100">
                {children}
            </div>
        </main>
        </body>
        </html>
    );
}